

import Vue from 'vue'
import FastClick from 'fastclick'
import App from './App'
import router from './router'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'


FastClick.attach(document.body)

Vue.config.productionTip = false

Vue.use(AjaxPlugin)
Vue.use(LoadingPlugin)
Vue.use(ToastPlugin)
Vue.use(AlertPlugin)
Vue.use(ConfirmPlugin)


Vue.prototype.glo = {
}

Vue.prototype.baseurl = ''


Vue.prototype.login = function(call) {

if (!this.glo.uid) {
const _this = this;
this.$http.get(_this.baseurl+'/phone/site/user',{withCredentials:true}).then(function(res){
res = res.data;
console.log(res);
if (res.status == 1) {
_this.glo = res.data;

if(!_this.glo.uid) {
console.log('登录失败');
}
call &&　call(_this.glo);
} else {
_this.$vux.toast.show({text:res.message});
}
}, function(err){
_this.$vux.toast.show({text:'获取用户信息失败'});
});
} else {
call &&　call(this.glo);
}
}

/* eslint-disable no-new */
new Vue({
router,
render: h => h(App)
}).$mount('#app-box')



